
main {
  margin: 0 17%;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.25);
  padding: 4em 2em 2em;
}
@media only screen and (max-width: 758px) {
main {
      margin: 0 5%;
}
}
main h1 {
    margin-bottom: 0;
}
main h2 {
    font-weight: 400;
    transition: letter-spacing .25s ease-out;
}
main h2:hover {
      letter-spacing: 4px;
      transition: letter-spacing .25s ease-out;
}
main small {
    font-size: .95rem;
    font-weight: 100;
    letter-spacing: 3px;
}
main .talks {
    position: relative;
    margin-top: 9.5%;
}
main .talks h4 {
      font-size: .85rem;
      font-weight: 100;
      letter-spacing: 2px;
      position: absolute;
      text-transform: uppercase;
      left: 0;
      top: 0;
      -webkit-transform: rotate(-90deg);
              transform: rotate(-90deg);
      transition: all .25s ease-out;
      text-align: right;
}
main .talks h4.hover {
        letter-spacing: 4px;
        transition: all .25s ease-out;
        -webkit-transform: translateX(-10%) rotate(-90deg);
                transform: translateX(-10%) rotate(-90deg);
}
main .talks ul > li {
      /*margin-left: 20%;*/
      text-align: left;
      margin-left: 20px;
      list-style: circle;
      padding-top: 3px;
}
main .talks ul > li a {
        transition: -webkit-transform .25s ease-out;
        transition: transform .25s ease-out;
        transition: transform .25s ease-out, -webkit-transform .25s ease-out;
}
main .talks ul > li a:hover {
          color: white;
          -webkit-transform: translateX(2%) skew(-9deg);
                  transform: translateX(2%) skew(-9deg);
          transition: -webkit-transform .25s ease-out;
          transition: transform .25s ease-out;
          transition: transform .25s ease-out, -webkit-transform .25s ease-out;
}



