
#logo {
  display: block;
  cursor: crosshair;
  min-height: 110.4px;
  position: relative;
  transition: all .25s ease-out;
}
#logo svg {
    max-width: 50px;
    transition: all .25s ease-out;
}
#logo .cls-1, #logo .cls-2, #logo .cls-3 {
    fill: none;
    stroke: #fff;
    stroke-miterlimit: 10;
    stroke-width: 1px;
}
#logo:hover {
    transition: all .25s ease-out;
}
#logo:hover .logo1, #logo:hover .logo2 {
      opacity: 1;
      transition: all .25s ease-out;
}
#logo:hover .logo1 {
      -webkit-animation: glitch-anim-1 2s infinite linear alternate-reverse;
              animation: glitch-anim-1 2s infinite linear alternate-reverse;
}
#logo:hover .logo3 {
      -webkit-animation: glitch-anim-2 2s infinite linear alternate-reverse;
              animation: glitch-anim-2 2s infinite linear alternate-reverse;
}
#logo .logo1, #logo .logo2, #logo .logo3 {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    min-width: 50px;
}
#logo .logo1 {
    left: 50%;
    opacity: 0;
}
#logo .logo1 .cls-1, #logo .logo1 .cls-2, #logo .logo1 .cls-3 {
      stroke: #FCFC36;
      stroke-width: 2px;
}
#logo .logo3 {
    opacity: 0;
    left: 75%;
}
#logo .logo3 .cls-1, #logo .logo3 .cls-2, #logo .logo3 .cls-3 {
      stroke: #FCFC36;
      stroke-width: 15px;
}
