
footer {
  font-size: .75rem;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
}
footer ul {
    /*max-width: 20%;*/
    /*margin: 0 auto;*/
}
footer ul li {
      list-style: none;
      margin-right: 20px;
      position: relative;
}
footer ul li:after {
        position: absolute;
        top: 0;
        right: -11px;
        content: ' - ';
}
footer ul li:last-child:after {
        content: ' ';
}



